<template>
	<div class="total">
		<vhead></vhead>
		<section class="total_wrap">
			<section class="header">
				<div class="title"></div>
			</section>
			<div>
				<section class="view">
					<div class="total-1">
						<span>
							<section class="address">
								<router-link to="/address">
									<p></p>
								</router-link>
								<i class="arrow"></i>
							</section>
						</span>
					</div>
					<div class="total-2">
						<section class="coupon">
							<header>
								<h2>/</h2>
								<i class="arrow"></i>
							</header>
						</section>
						<section class="maidou">
							<header>
								<h2></h2>
							</header>
							<div class="has-maidou">
								<span>
									<span>0</span>,
									<span>0</span></span>
								<input type="checkbox">
							</div>
						</section>
					</div>
					<section class="invoice" id="invoice">
						<header>
							<h2></h2>
							<i class="arrow" @click="toggle" ref="arrowchange"></i>
						</header>
						<div v-if="show">
							<input type="text" class="c-text" placeholder="">
						</div>
					</section>
					<section class="message">
						<textarea placeholder=""></textarea>
					</section>
					<section class="pay">
						<header>
							<h2></h2>
						</header>
						<div class="item-box">
							<div class="item">
								<div href="#" class="item-icon">
									<img src="/static/images/login/ico_alipay.png" alt="">
								</div>
								<div class="pay-item">
									<span class="pay-name"> </span>
									<span class="pay-going">, </span>
								</div>
							</div>
						</div>
					</section>
					<footer class="pay">
						<div class="count">
							<ul class="price-list">
								<li>
									<span>{{money}}</span>
								</li>
								<li>
									<span>-
										<span>0.00</span>
									</span>
								</li>
								<li>
									<span>-
										<span>0.00</span>
									</span>
								</li>
								<li>
									<span>{{money}}</span> +
									<span>0.00</span>() =
									<span class="totalmoney">{{money}}</span>
								</li>
							</ul>
							<a href="#" class="btn"></a>
						</div>
					</footer>
				</section>
			</div>
		</section>
		<vfooter></vfooter>
	</div>
</template>

<script>
import vhead from '../header/Header'
import vfooter from '../footer/Footer'
export default {
	components:{
		vhead,
		vfooter
	},
	data() {
		return {
			show: false,
			cartList:[],
			money:''
		}
	},
	methods: {
		toggle(){
			this.show = !this.show
		}
	},
	created(){
		this.money = this.$store.state.money.toFixed(2)
		console.log(this.money)
	}
}
</script>
<style lang="stylus" scoped>
.header 
    height:0.5333rem
    line-height:0.5333rem
    color:#666
    background-color:#fff
    text-align:center
    padding:1.2rem 0.2133rem 0.2133rem 0.2133rem
    border-bottom:1px solid #ddd
	
.view 
    font-family:""
    font-size:0.32rem
    line-height:0.5867rem
    padding:0.2667rem 0.1333rem
	.total-1,.total-2,.invoice,.message
		background-color: #f5f5f5
		border: 1px solid #c8c8c8
		border-radius:0.1333rem
		position:relative
		margin: 0 auto
		overflow:hidden
		margin-bottom:0.2667rem
	.address
		padding:0.2667rem
		font-size:0.32rem
		background-color:#f5f5f5;
		border-radius:0.1333rem
		a
			color: rgb(51, 51, 51)
	.coupon
		border-top-left-radius:0.1333rem
		border-top-right-radius:0.1333rem
		border-bottom:1px solid #c8c8c8
	.coupon,
	.maidou 
		background-color:#f5f5f5
		padding:0.2667rem
	.maidou
		border-bottom-left-radius:0.1333rem
		border-bottom-right-radius:0.1333rem
		position: relative
		h2
			width :40%
			float: left
		.has-maidou
			float:left
			text-align:right
			right:0.2667rem
			width:60%
			height:0.5867rem
			line-height:0.5867rem	
			span
				text-align:right
				color:#7e7e7e
	.arrow
		&:before 
			content:''
			width:0.32rem
			height:0.32rem
			display:block
			border-right: 1px solid #999
			border-bottom: 1px solid #999
			transform: rotate(-45deg)
			position:absolute
			right:0.5333rem
			top:0.4rem			
.message
    margin: 0 auto
	textarea
		outline: none
		border:none
		height:0.5333rem
		line-height:0.5333rem
		resize:none
		width:94%
		padding:0.2667rem
		border-radius:0.1333rem
		background-color: #f5f5f5
.invoice
	padding:0.2667rem 0.2667rem 0.2667rem 0.2667rem
	.c-text
		width: 90%
		height: 0.8rem
		padding-left: 0.2667rem
		margin-top: 0.2667rem
.pay
    padding:0px 0.2667rem 0 0.2667rem
	.count
		padding:0
	.item-box
		margin:0.1333rem 0.5333rem
		.item
			position:relative
			width:100%
			height:1.0667rem
			padding :0.1067rem 0.1067rem
			background-color:#f5f5f5
			text-align:left
			margin :0 auto
			border: 0.0267rem solid #C8C8C8
			border-color: #d31d50
			border-radius: 0.1333rem
			font-size: 0.32rem
			line-height:0.5333rem
			img 
				display:inline-block
				float :left
				width:1.0667rem
				height:1.0667rem
			.pay-name
				font-size:0.3733rem
				font-weight:700
				color:#333
			.pay-going
				color:#7e7e7e
				font-size:0.3467rem
			.pay-name,
			.pay-going	
				display:inline-block
				float: right
				width :85%
				height:0.5867rem
				line-height:0.5867rem
	.price-list 
		margin: 0.2667rem 0 0.2667rem 0.2667rem
		text-align:left
		.totalmoney 
			font-size:0.64rem
			color:#d31d50
			font-weight:bolder
	.btn 
		width:70%
		height:0.5867rem
		display:inline-block
		margin:0.5333rem 10%
		text-align:center
		color:#fff
		padding:0.32rem 0.5867rem
		border-radius:0.1333rem
		font-size:0.4267rem
		background-color:#d41c50
	



    
</style>



